/deep/ .page .page10,
/deep/ .page .page9,
/deep/ .page .page8,
/deep/ .page .page7,
/deep/ .page .page6,
/deep/ .page .page5,
/deep/ .page .page4,
/deep/ .page .page2,
/deep/ .page .page1,
/deep/ .page .page3 {
    position: absolute;
    z-index: 2;
    width: 100vw;
    height: 100vh;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    filter: blur(9px);
}

