.myPage {
    position: absolute;
    z-index: 3;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;

    .clearCloud {
        position: absolute;
        padding: 5px 8px;
        border-radius: 2px;
        background-color: #f60;
        color: #fff;
        left: 43%;
        top: 15%;
        cursor: pointer;
        z-index: 99;

        &:hover {
            background-color: #fe2d46;
        }
    }

    .box-x::before {
        content: '';
        position: absolute;
        left: 49%;
        top: 50%;
        width: 40px;
        height: 1px;
        background-color: #9195a3
    }

    .box-x::after {
        content: '';
        position: absolute;
        left: 50%;
        top: 48%;
        width: 1px;
        height: 40px;
        background-color: #9195a3
    }

    .notice {
        text-align: right;
        color: #faa90e;
        font-weight: bold;
        padding-right: 100px;
    }

    .pro.left-title {
        padding-left: 240px;
    }

    .zt {
        line-height: 2;

    }

    .name {
        font-size: 16px;
        margin-top: 20%;
        text-align: center;
        color: #333
    }

    .zt-top {
        margin-top: 14%;
    }



    .gifImg {
        width: 40vw;
        height: 35vh;

        padding-left: 240px;

        img,
        video {
            width: 76%;

        }

        .app {
            width: 96%;
        }
    }

    .ts-div {
        text-align: center;

        video {
            width: 66%;
        }
    }

    .appImg {
        width: 45vw;
    }

    .gifImg-right {
        padding-left: 0;
    }

    .desc-div {
        padding-top: 20px;
        width: 40%;
        text-align: left;
    }

    .desc-div-left {
        padding-left: 240px;
    }

    .desc-li {
        line-height: 2;
        font-size: 30px;

        .number {
            color: #f60;
            font-family: 'number'
        }
    }

    .ts.left-title {
        padding-left: 200px;
    }

    .ts-div {
        height: 50%;
        width: 100%;
        padding: 0 200px;
        box-sizing: border-box;

        img {
            width: 100%;
            height: 100%;
        }
    }

    .ts-desc {
        margin-top: 50px;
    }

    .good-bad {
        color: #fff;
        line-height: 30px;
        font-size: 20px;

        .good-div,
        .bad-div {
            // width: 48%;
            padding: 10px;
            height: 40vh;
            box-sizing: border-box;
        }

        .good-div {
            padding-top: 5%;

            .pro-list::after {
                background-color: #006d00;
            }

            .pro-list {
                margin-left: 110px;
            }
        }

        .bad-div {
            margin-left: 56%;
            margin-top: 4%;

            .pro-list::after {
                background-color: orangered;
            }

            .pro-list {
                margin-left: 0;
            }
        }
    }

    .line {
        width: 5px;
        height: 64%;
        background-color: #baba00;
        position: absolute;
        top: 14%;
        left: 50%;
        transform: rotate(45deg);
    }

    .thanks {
        position: absolute;
        left: 50%;
        top: 50%;
        color: #fff;
        font-size: 71px;
        transform: translate(-50%, -50%);
    }
}

