}

.echart-fp {
    width: 48vw;
    height: 35vh;
    margin-top: 0;
}

.item {
    display: flex;
}

.top1 {
    color: #fe2d46
}

.top2 {
    color: #faa90e
}

.top3 {
    color: #333
}

.top4 {
    color: #00af00
}

.top5 {
    color: #00af00
}

.calc-div {
    float: right;
    width: 32vw;
    line-height: 30px;
    padding-top: 50px;

    .calc-item {
        font-size: 32px;
        line-height: 2.5;
        text-align: left;
        margin-left: 46px;
        position: relative;

        &::after {
            position: absolute;
            content: "";
            width: 10px;
            height: 10px;
            display: block;
            border: 5px #0084e2 solid;
            border-top-color: transparent;
            border-right-color: transparent;
            left: -48px;
            top: 46px;
        }



    }

    .number {
        padding: 20px;
        font-size: 40px;
        color: orangered;
        font-family: 'number'
    }

    .top {
        margin-top: 30px;

        .title {
            line-height: 2;
            border-radius: 4px;
            background: #0084e2;
            color: #fff;
            padding: 5px 8px;
            font-size: 20px;
        }

        .top-list {
            margin-top: 20px;
            margin-left: 100px;
        }

        .topli {
            line-height: 2;
            font-size: 32px;
            position: relative;

            .number {
                font-size: 32px;
            }

            .box {
                position: absolute;
                width: 30px;
                height: 30px;
                display: block;
                background-color: hotpink;
                border-radius: 2px;
                left: -48px;
                top: 19px;
                line-height: 30px;
                color: #fff;
                font-size: 22px;
                text-align: center;
            }

            .top1 {
                background-color: #fe2d46;
            }

            .top2 {
                background-color: #f60;
            }

            .top3 {
                background-color: #faa90e;
            }

            .top4 {
                background-color: #9195a3;
            }
        }
    }
}

