.pro-list {
    position: relative;
    line-height: 60px;
    margin-left: 42%;

    &::after {
        position: absolute;
        content: '';
        width: 15px;
        height: 15px;
        top: 22px;
        left: -35px;
        background-color: #0346ff;
    }
}

.extra-img {
    display: flex;
    justify-content: space-around;

    .left-img {
        width: 48vw;
        padding: 0 50px;
        box-sizing: border-box;

        img {
            width: 100%;
        }
    }

    .notice {
        color: #0084e2;
        text-align: center;
        font-size: 14px;
    }
}

.pro-list.extra {
    margin-left: 37%;
}

.left {
    float: left;
}

.right {
    float: right;
}

.left-title {
    text-align: left;
    padding-left: 15%;
}

.left-padding {
